import NavigatorView from './Navigator.view';
import VideoCardsController from '../VideoCard/VideoCards.controller';

export default class NavigatorController {
  static eventButtonNext() {
    const clipsCountPage = window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--clips-count-page');
    const clipsTotal = window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--clips-total');
    const position = window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--position');
    document.querySelector('.clip-list').style.setProperty('--position', parseInt(position, 10) + 1);

    const next = document.getElementById('next');
    next.setAttribute('data-page-num', parseInt(next.getAttribute('data-page-num'), 10) + 1);
    next.children[0].textContent = next.getAttribute('data-page-num');

    const current = document.getElementById('current');
    current.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) + 1);
    current.textContent = current.getAttribute('data-page-num');

    const prev = document.getElementById('prev');
    prev.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) - 1);
    prev.classList.remove('-hidden');
    prev.children[0].textContent = prev.getAttribute('data-page-num');

    if (current.getAttribute('data-page-num') > 2) {
      const beforePrev = document.getElementById('before-prev');
      beforePrev.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) - 2);
      beforePrev.classList.remove('-hidden');
      beforePrev.children[0].textContent = beforePrev.getAttribute('data-page-num');
    }

    if (clipsTotal - clipsCountPage * position <= 15) {
      document.querySelector('.clip-list').style.setProperty('--clips-total', parseInt(clipsTotal, 10) + 15);
      VideoCardsController.loadData();
    }

    if (clipsCountPage * position > clipsTotal) {
      next.classList.add('hidden');
    }
  }

  static eventButtonPrev() {
    const position = window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--position');
    document.querySelector('.clip-list').style.setProperty('--position', parseInt(position, 10) - 1);

    const current = document.getElementById('current');
    current.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) - 1);
    current.textContent = current.getAttribute('data-page-num');

    const prev = document.getElementById('prev');
    prev.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) - 1);
    prev.children[0].textContent = prev.getAttribute('data-page-num');

    const beforePrev = document.getElementById('before-prev');
    beforePrev.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) - 2);
    beforePrev.children[0].textContent = beforePrev.getAttribute('data-page-num');

    const next = document.getElementById('next');
    next.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) + 1);
    next.children[0].textContent = next.getAttribute('data-page-num');

    if (current.getAttribute('data-page-num') < 3) {
      beforePrev.classList.add('-hidden');
    }

    if (current.getAttribute('data-page-num') < 2) {
      prev.classList.add('-hidden');
    }
  }

  static eventButtonBeforePrev() {
    const position = window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--position');
    document.querySelector('.clip-list').style.setProperty('--position', parseInt(position, 10) - 2);

    const current = document.getElementById('current');
    current.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) - 2);
    current.textContent = current.getAttribute('data-page-num');

    const prev = document.getElementById('prev');
    prev.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) - 1);
    prev.children[0].textContent = prev.getAttribute('data-page-num');

    const beforePrev = document.getElementById('before-prev');
    beforePrev.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) - 2);
    beforePrev.children[0].textContent = beforePrev.getAttribute('data-page-num');

    const next = document.getElementById('next');
    next.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) + 1);
    next.children[0].textContent = next.getAttribute('data-page-num');

    if (current.getAttribute('data-page-num') < 3) {
      beforePrev.classList.add('-hidden');
    }

    if (current.getAttribute('data-page-num') < 2) {
      prev.classList.add('-hidden');
    }
  }

  static start() {
    NavigatorView.render();

    document.getElementById('next').addEventListener('click', NavigatorController.eventButtonNext);
    document.getElementById('prev').addEventListener('click', NavigatorController.eventButtonPrev);
    document.getElementById('before-prev').addEventListener('click', NavigatorController.eventButtonBeforePrev);
  }
}
