export default class VideoCardsModel {
  constructor(url) {
    this.url = url;
    this.infoAboutVideos = [];
  }

  static getVideoId(data) {
    return data.items.map(clip => clip.id.videoId);
  }

  static truncateWithEllipses(text, max) {
    return text.substr(0, max - 1) + (text.length > max ? '&hellip;' : '');
  }

  static extractNextPageToken(data) {
    return data.nextPageToken;
  }

  static getVideoInformation(data) {
    const oneVideo = {};
    oneVideo.image = data.snippet.thumbnails.medium;
    oneVideo.channelTitle = VideoCardsModel.truncateWithEllipses(data.snippet.channelTitle, 27);
    oneVideo.publishedAt = data.snippet.publishedAt.slice(0, 10);
    oneVideo.viewCount = data.statistics.viewCount;
    oneVideo.description = VideoCardsModel.truncateWithEllipses(data.snippet.description, 180);
    oneVideo.videoTitle = data.snippet.title;
    oneVideo.videoLink = `https://www.youtube.com/watch?v=${data.id}`;
    return oneVideo;
  }

  async getVideoCards() {
    const response = await fetch(this.url);
    const data = await response.json();
    const secondResponse = await fetch(`https://www.googleapis.com/youtube/v3/videos?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&id=${VideoCardsModel.getVideoId(data).join(',')}&part=snippet,statistics`);
    const secondData = await secondResponse.json();

    for (let i = 0; i < 15; i += 1) {
      this.infoAboutVideos.push(VideoCardsModel.getVideoInformation(secondData.items[i]));
    }

    this.infoAboutVideos.push(VideoCardsModel.extractNextPageToken(data));

    return this.infoAboutVideos;
  }
}
