import VideoCardsView from './VideoCards.view';
import VideoCardsModel from './VideoCards.model';

export default class VideoCardsController {
  constructor(searchQuery) {
    this.url = `https://www.googleapis.com/youtube/v3/search?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&type=video&part=snippet&maxResults=15&q=${searchQuery}`;
    VideoCardsController.query = searchQuery || VideoCardsController.query;
  }

  static eventDragAndDrop() {
    const clipList = document.querySelector('.clip-list');
    let isDown = false;
    let startX;
    let dragDrop = 0;

    function mouseDownSwipe(e) {
      // eslint-disable-next-line no-unused-vars
      const position = window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--position');
      const clipsCountPage = window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--clips-count-page');
      // eslint-disable-next-line no-unused-vars
      const clipWidth = document.documentElement.clientWidth / clipsCountPage;

      clipList.style.left = `${-1 * position * clipsCountPage * clipWidth}px`;
      isDown = true;
      startX = e.clientX || e.changedTouches[0].clientX;
      clipList.classList.add('active');
    }

    function mouseUpSwipe() {
      const position = window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--position');
      const clipsCountPage = window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--clips-count-page');
      const clipsTotal = window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--clips-total');
      clipList.style.left = `${0}px`;
      isDown = false;
      clipList.classList.remove('active');
      if (dragDrop < -200) {
        document.querySelector('.clip-list').style.setProperty('--position', parseInt(position, 10) + 1);
        const next = document.getElementById('next');
        next.setAttribute('data-page-num', parseInt(next.getAttribute('data-page-num'), 10) + 1);
        next.children[0].textContent = next.getAttribute('data-page-num');

        const current = document.getElementById('current');
        current.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) + 1);
        current.textContent = current.getAttribute('data-page-num');

        const prev = document.getElementById('prev');
        prev.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) - 1);
        prev.classList.remove('-hidden');
        prev.children[0].textContent = prev.getAttribute('data-page-num');

        if (current.getAttribute('data-page-num') > 2) {
          const beforePrev = document.getElementById('before-prev');
          beforePrev.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) - 2);
          beforePrev.classList.remove('-hidden');
          beforePrev.children[0].textContent = beforePrev.getAttribute('data-page-num');
        }

        if (clipsTotal - clipsCountPage * position <= 15) {
          document.querySelector('.clip-list').style.setProperty('--clips-total', parseInt(clipsTotal, 10) + 15);
          VideoCardsController.loadData();
        }

        if (clipsCountPage * position > clipsTotal) {
          next.classList.add('hidden');
        }
        dragDrop = 0;
      } else if (dragDrop > 200 && position > 0) {
        document.querySelector('.clip-list').style.setProperty('--position', parseInt(position, 10) - 1);

        const current = document.getElementById('current');
        current.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) - 1);
        current.textContent = current.getAttribute('data-page-num');

        const prev = document.getElementById('prev');
        prev.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) - 1);
        prev.children[0].textContent = prev.getAttribute('data-page-num');

        const beforePrev = document.getElementById('before-prev');
        beforePrev.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) - 2);
        beforePrev.children[0].textContent = beforePrev.getAttribute('data-page-num');

        const next = document.getElementById('next');
        next.setAttribute('data-page-num', parseInt(current.getAttribute('data-page-num'), 10) + 1);
        next.children[0].textContent = next.getAttribute('data-page-num');

        if (current.getAttribute('data-page-num') < 3) {
          beforePrev.classList.add('-hidden');
        }

        if (current.getAttribute('data-page-num') < 2) {
          prev.classList.add('-hidden');
        }
      }
    }

    function mouseMoveSwipe(e) {
      if (!isDown) return;
      const position = window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--position');
      const clipsCountPage = window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--clips-count-page');
      const clipWidth = document.documentElement.clientWidth / clipsCountPage;
      dragDrop = (e.clientX || e.changedTouches[0].clientX) - clipList.scrollLeft - startX;
      clipList.style.left = `${-1 * position * clipsCountPage * clipWidth + dragDrop}px`;
      window.console.log(dragDrop);
      window.console.log(e);
    }

    clipList.addEventListener('mousedown', mouseDownSwipe, false);
    clipList.addEventListener('touchstart', mouseDownSwipe, false);

    clipList.addEventListener('mouseup', mouseUpSwipe, false);
    clipList.addEventListener('touchend', mouseUpSwipe, false);

    clipList.addEventListener('mousemove', mouseMoveSwipe, false);
    clipList.addEventListener('touchmove', mouseMoveSwipe, false);
  }

  static resize() {
    let clipsCountPagePrev = parseInt(window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--clips-count-page'), 10);

    function ClousureResize() {
      const clipsCountPageCurrent = parseInt(window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--clips-count-page'), 10);
      const position = parseInt(window.getComputedStyle(document.querySelector('.clip-list')).getPropertyValue('--position'), 10);

      if (clipsCountPageCurrent !== clipsCountPagePrev) {
        const currentVideoPosition = clipsCountPagePrev * position;

        const newPosition = Math.floor(currentVideoPosition / clipsCountPageCurrent);
        document.querySelector('.clip-list').style.setProperty('--position', newPosition);
        clipsCountPagePrev = clipsCountPageCurrent;

        const current = document.getElementById('current');
        current.setAttribute('data-page-num', newPosition + 1);
        current.textContent = current.getAttribute('data-page-num');

        const next = document.getElementById('next');
        next.setAttribute('data-page-num', newPosition + 2);
        next.children[0].textContent = next.getAttribute('data-page-num');

        const prev = document.getElementById('prev');
        prev.setAttribute('data-page-num', newPosition);
        prev.children[0].textContent = prev.getAttribute('data-page-num');

        const beforePrev = document.getElementById('before-prev');
        beforePrev.setAttribute('data-page-num', newPosition - 1);
        beforePrev.children[0].textContent = beforePrev.getAttribute('data-page-num');
      }
    }

    window.addEventListener('resize', ClousureResize);
  }

  async start() {
    const model = new VideoCardsModel(this.url);
    const videos = await model.getVideoCards();

    VideoCardsController.nextPagetoken = videos[videos.length - 1];

    const view = new VideoCardsView(videos);
    view.render('new');

    VideoCardsController.eventDragAndDrop();
    VideoCardsController.resize();
  }

  static async loadData() {
    const model = new VideoCardsModel(`https://www.googleapis.com/youtube/v3/search?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&type=video&part=snippet&maxResults=15&q=${VideoCardsController.query}&pageToken=${VideoCardsController.nextPagetoken}`);
    const videos = await model.getVideoCards();

    VideoCardsController.nextPagetoken = videos[videos.length - 1];

    const view = new VideoCardsView(videos);
    view.render();
  }
}
