export default class VideoCardsView {
  constructor(state) {
    this.state = state;
  }

  render(stateSearch) {
    let clipList;
    if (stateSearch === 'new') {
      if (!document.querySelector('.clip-list')) {
        clipList = document.createElement('ul');
        clipList.classList.add('clip-list');
        document.body.insertBefore(clipList, document.querySelector('.navigator'));
      } else {
        clipList = document.querySelector('.clip-list');
        document.body.removeChild(clipList);
        clipList = document.createElement('ul');
        clipList.classList.add('clip-list');
        document.body.insertBefore(clipList, document.querySelector('.navigator'));
      }
    } else {
      clipList = document.querySelector('.clip-list');
    }

    for (let i = 0; i < 15; i += 1) {
      const li = document.createElement('li');
      li.classList.add('clip');
      clipList.appendChild(li);

      const clipInner = document.createElement('div');
      clipInner.classList.add('clip-inner');
      li.appendChild(clipInner);

      const image = document.createElement('img');
      image.classList.add('clip-image');
      image.setAttribute('src', `${this.state[i].image.url}`);
      image.setAttribute('width', `${this.state[i].image.width}`);
      image.setAttribute('alt', `${this.state[i].videoTitle}`);
      clipInner.appendChild(image);

      const clipTitle = document.createElement('div');
      clipTitle.classList.add('clip-title');
      clipInner.appendChild(clipTitle);

      const clipLink = document.createElement('a');
      clipLink.classList.add('clip-link');
      clipLink.setAttribute('href', `${this.state[i].videoLink}`);
      clipLink.setAttribute('target', '_blank');
      clipLink.innerHTML = `${this.state[i].videoTitle}`;
      clipTitle.appendChild(clipLink);

      const clipInfo = document.createElement('div');
      clipInfo.classList.add('clip-info');
      clipInner.appendChild(clipInfo);

      const clipInfoWrapper = document.createElement('div');
      clipInfoWrapper.classList.add('clip-info-wrapper');
      clipInfo.appendChild(clipInfoWrapper);

      const clipChannelTitle = document.createElement('div');
      clipChannelTitle.classList.add('clip-channel-title');
      clipChannelTitle.classList.add('clip-icon');
      clipChannelTitle.innerHTML = `${this.state[i].channelTitle}`;
      clipInfoWrapper.appendChild(clipChannelTitle);

      const clipDate = document.createElement('div');
      clipDate.classList.add('clip-date');
      clipDate.classList.add('clip-icon');
      clipDate.innerHTML = `${this.state[i].publishedAt}`;
      clipInfoWrapper.appendChild(clipDate);

      const clipViewCount = document.createElement('div');
      clipViewCount.classList.add('clip-view-count');
      clipViewCount.classList.add('clip-icon');
      clipViewCount.innerHTML = `${this.state[i].viewCount}`;
      clipInfoWrapper.appendChild(clipViewCount);

      const clipDescription = document.createElement('div');
      clipDescription.classList.add('clip-description');
      clipDescription.innerHTML = `${this.state[i].description}`;
      clipInfo.appendChild(clipDescription);
    }
  }
}
