export default class NavigatorView {
  static render() {
    if (document.querySelector('.navigator')) {
      document.body.removeChild(document.querySelector('.navigator'));
    }
    const navigator = document.createElement('div');
    navigator.classList.add('navigator');
    document.body.appendChild(navigator);

    const beforePrev = document.createElement('button');
    beforePrev.innerHTML = '&nbsp;';
    beforePrev.setAttribute('class', 'button before-prev -hidden');
    beforePrev.setAttribute('id', 'before-prev');
    beforePrev.setAttribute('data-page-num', '1');
    navigator.appendChild(beforePrev);

    const beforePrevSpan = document.createElement('span');
    beforePrevSpan.setAttribute('class', 'tooltip');
    beforePrevSpan.innerHTML = beforePrev.getAttribute('data-page-num');
    beforePrev.appendChild(beforePrevSpan);

    const prev = document.createElement('button');
    prev.innerHTML = '&nbsp;';
    prev.setAttribute('class', 'button prev -hidden');
    prev.setAttribute('id', 'prev');
    prev.setAttribute('data-page-num', '2');
    navigator.appendChild(prev);

    const prevSpan = document.createElement('span');
    prevSpan.setAttribute('class', 'tooltip');
    prevSpan.innerHTML = prev.getAttribute('data-page-num');
    prev.appendChild(prevSpan);

    const current = document.createElement('button');
    current.setAttribute('class', 'button current');
    current.setAttribute('id', 'current');
    current.setAttribute('data-page-num', '1');
    current.innerHTML = current.getAttribute('data-page-num');
    navigator.appendChild(current);

    const next = document.createElement('button');
    next.innerHTML = '&nbsp;';
    next.setAttribute('class', 'button next');
    next.setAttribute('id', 'next');
    next.setAttribute('data-page-num', '2');
    navigator.appendChild(next);

    const nextSpan = document.createElement('span');
    nextSpan.setAttribute('class', 'tooltip');
    nextSpan.innerHTML = next.getAttribute('data-page-num');
    next.appendChild(nextSpan);
  }
}
