import SearchBoxView from './SearchBox.view';
import VideoCardsController from '../VideoCard/VideoCards.controller';
import NavigatorController from '../Navigator/Navigator.controller';

export default class SearchBoxController {
  static async searchEnter(e) {
    if (e.keyCode === 13) {
      const videoCardsController = new VideoCardsController(document.querySelector('.search-form').value);
      await videoCardsController.start();
      await NavigatorController.start();
    }
  }

  static start() {
    SearchBoxView.render();

    document.querySelector('.search-form').addEventListener('keydown', SearchBoxController.searchEnter);
  }
}
