export default class SearchBoxView {
  static render() {
    const searchBoxWrapper = document.createElement('div');
    const searchBox = document.createElement('input');

    searchBoxWrapper.classList.add('search-form-wrapper');

    searchBox.setAttribute('type', 'text');
    searchBox.setAttribute('placeholder', 'Input title of Video and press Enter');
    searchBox.setAttribute('class', 'search-form');

    searchBoxWrapper.appendChild(searchBox);
    document.body.appendChild(searchBoxWrapper);
  }
}
